$black: #262626;
$gray: #EBEBEB;
$red: #ef041f;
$blue: rgb(88,193,217);
$pc_width: 1170px;
$single_pc_width: 1170px;
$award_article_width: 720px;
$header-height: 55px;

//== Media Queries
// media query
$breakpoints: (
  'sm': 'screen and (max-width: 540px)',
  'md': 'screen and (max-width: 768px)',
  'lg': 'screen and (max-width: 1080px)',
  'xl': 'screen and (max-width: 1440px)',
) !default;

@mixin mq($breakpoint: md) {
	@media #{map-get($breakpoints, $breakpoint)} {
		@content;
	}
}

// line-height
@mixin lhCrop($line-height) {
  &::before {
    content: '';
    display: block;
    height: 0;
    width: 0;
    margin-top: calc((1 - #{$line-height}) * 0.5em);
  }
}

//
// Ceaser/developer/ceaser-easings.scss
// https://github.com/matthewlein/Ceaser/blob/master/developer/ceaser-easings.scss
//

// Cubic
$easeInCubic: cubic-bezier(0.55, 0.055, 0.675, 0.19);
$easeOutCubic: cubic-bezier(0.215, 0.61, 0.355, 1);
$easeInOutCubic: cubic-bezier(0.645, 0.045, 0.355, 1);

// Circular
$easeInCirc: cubic-bezier(0.6, 0.04, 0.98, 0.335);
$easeOutCirc: cubic-bezier(0.075, 0.82, 0.165, 1);
$easeInOutCirc: cubic-bezier(0.785, 0.135, 0.15, 0.86);

// Exponential
$easeInExpo: cubic-bezier(0.95, 0.05, 0.795, 0.035);
$easeOutExpo: cubic-bezier(0.19, 1, 0.22, 1);
$easeInOutExpo: cubic-bezier(1, 0, 0, 1);

// Quadratic
$easeInQuad: cubic-bezier(0.55, 0.085, 0.68, 0.53);
$easeOutQuad: cubic-bezier(0.25, 0.46, 0.45, 0.94);
$easeInOutQuad: cubic-bezier(0.455, 0.03, 0.515, 0.955);

// Quartic
$easeInQuart: cubic-bezier(0.895, 0.03, 0.685, 0.22);
$easeOutQuart: cubic-bezier(0.165, 0.84, 0.44, 1);
$easeInOutQuart: cubic-bezier(0.77, 0, 0.175, 1);

// Quintic
$easeInQuint: cubic-bezier(0.755, 0.05, 0.855, 0.06);
$easeOutQuint: cubic-bezier(0.23, 1, 0.32, 1);
$easeInOutQuint: cubic-bezier(0.86, 0, 0.07, 1);

// Sine
$easeInSine: cubic-bezier(0.47, 0, 0.745, 0.715);
$easeOutSine: cubic-bezier(0.39, 0.575, 0.565, 1);
$easeInOutSine: cubic-bezier(0.445, 0.05, 0.55, 0.95);

// Back
$easeInBack: cubic-bezier(0.6, -0.28, 0.735, 0.045);
$easeOutBack: cubic-bezier(0.175, 0.885, 0.32, 1.275);
$easeInOutBack: cubic-bezier(0.68, -0.55, 0.265, 1.55);


// basic

@mixin wrapper-set() {
	margin-left: auto;
	margin-right: auto;
	max-width: $pc_width;
	box-sizing: border-box;
	padding-left: 15px;
	padding-right: 15px;
}

@mixin background($position-x,$position-y) {
	background-position: $position-x $position-y;
	background-repeat: no-repeat;
	background-size: cover;
}

@mixin appearance() {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}

@mixin animation($seconds,$function) {
	-webkit-transition: all $seconds;
	-moz-transition: all $seconds;
	-ms-transition: all $seconds;
	 -o-transition: all $seconds;
	transition: all $seconds;
	-webkit-transition-timing-function: $function;
	transition-timing-function: $function;
}

// box layout

@mixin flex($wrap,$justify,$align) {
	display: -webkit-flex;
	display: flex;
	flex-wrap: $wrap;
	-webkit-justify-content: $justify;
	justify-content: $justify;
	align-items: $align;
	box-lines: multiple;
}

@mixin center50() {
	position: relative;
	top: 50%;
	left: 50%;
	width: auto;
	height: auto;
	-webkit-transform: translate(-50%,-50%);
	-ms-transform: translate(-50%,-50%);
	transform: translate(-50%,-50%);
}

@mixin center50-y() {
	top: 50%;
	width: auto;
	height: auto;
	-webkit-transform: translateY(-50%);
	-ms-transform: translateY(-50%);
	transform: translateY(-50%);
}

@mixin center50-w() {
	left: 50%;
	width: auto;
	height: auto;
	-webkit-transform: translateW(-50%);
	-ms-transform: translateW(-50%);
	-moz-transform: translateW(-50%);
	transform: translateW(-50%);
}
