// ==========================================================================
// Foundation
// ==========================================================================

@import "_reset";
@import "foundation/mixin";

$single-width: 750px;
$middle-width: 1140px;

html {
  font-size: 62.5%;
}

body {
  font-family: "Helvetica", "Noto Sans Japanese", メイリオ, MSゴシック, sans-serif;
  line-height: 1.8;
  color: #4B5157;
  background-color: #EDEFF0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-feature-settings: "palt";
  font-feature-settings: "palt";
  font-size: 10px;
  font-size: 1rem;
  font-weight: 500;
  letter-spacing: normal;
  text-justify: inter-ideograph;
  min-height: calc(var(--vh, 1vh) * 100);
}

a {
  color: $black;
  text-decoration: none;
  transition: color 0.2s $easeInCubic;

  &:hover {
    color: $red;
    text-decoration: none;
  }
}

img {
  max-width: 100%;
}

p {
  @include lhCrop(1.8);
}

.container {
  max-width: 1366px;
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
  @include mq(md) {
    max-width: auto;
    margin-left: 0;
    margin-right: 0;
  }
}

.single-size {
  max-width: $single-width;
  margin-left: auto;
  margin-right: auto;
  @include mq(md) {
    max-width: initial;
    margin-left: 0;
    margin-right: 0;
    padding-left: 30px;
    padding-right: 30px;
  }
}

.middle-size {
  max-width: $middle-width;
  margin-left: auto;
  margin-right: auto;
  @include mq(md) {
    max-width: initial;
    margin-left: 0;
    margin-right: 0;
  }
}

section > .section-container {
  padding-top: 80px;
  padding-bottom: 80px;
  @include mq(md) {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  > h2 {
    font-size: 4rem;
    font-weight: 700;
    margin-bottom: 80px;
    line-height: 1;
    text-align: center;
    @include mq(md) {
      font-size: 2rem;
      margin-bottom: 48px;
    }
  }

  > h3 {
    text-align: center;
    font-size: 1.8rem;
    font-weight: 700;
    @include mq(md) {
      font-size: 1.3rem;
    }
  }

  .section-body {
    font-size: 1.8rem;
    @include mq(md) {
      font-size: 1.3rem;
    }

    a {
      color: $red;
      text-decoration: underline;
    }

    > h3 {
      font-size: 2.5rem;
      font-weight: 700;
      @include mq(md) {
        font-size: 1.6rem;
      }
    }

    > .lead {
      font-size: 1.8rem;
    }

    > * {
      margin-bottom: 48px;
      @include mq(md) {
        margin-bottom: 24px;
      }
    }

    p {
      text-align: justify;
      font-weight: 300;

      &:last-of-type {
        margin-bottom: 0;
      }

      small {
        display: block;
        font-size: 1.4rem;
        @include mq(md) {
          font-size: 1.2rem;
        }
      }
    }

    figure {
      text-align: center;
      figcaption {
        font-size: 1.2rem;
      }
    }

    .table-container {
      border: 1px solid #E0DEDD;
      border-radius: 10px;
      padding: 0 32px;
    }

    table {
      display: table;
      box-sizing: border-box;
      table-layout: auto;
      width: 100%;
      border-collapse: collapse;


      tbody {
        tr {
          border-bottom: 2px solid #F6F4F3;
        }
        th,
        td {
          text-align: left;
          font-size: 1.6rem;
          padding: 40px 0;
          word-wrap: break-word;
          @include mq(md) {
            font-size: 1.2rem;
            padding: 20px 0;
          }

        }

        th {
          font-weight: 700;
          width: 200px;
          @include mq(md) {
            width: 30%;
            padding-right: 32px;
          }
        }
        td {
          font-weight: 300;
        }
      }
    }
  }
}

.white-background {
  background-color: #fff;
}

.embed-card-links {
  display: flex;
  align-items: stretch;
  justify-content: flex-start;
  flex-wrap: wrap;
  @include mq(md) {
    display: block;
  }

  .cards {
    box-sizing: border-box;
    flex-basis: 32%;
    margin-right: 2%;
    padding: 8px 16px;
    background-color: #FFF;
    border-radius: 5px;
    margin-bottom: 2%;
    @include mq(md) {
      margin-right: 0;
      margin-bottom: 24px;
    }

    &:nth-of-type(3n) {
      margin-right: 0;
    }
  }
}

ol {
  margin-left: 1em;

  li {
    list-style: decimal outside;
    margin-bottom: 18px;
    @include mq(md) {
      margin-bottom: 6px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}

ul {
  margin-left: 1em;

  li {
    list-style: square outside;
  }
}

#footer {
  ul {
    li {
      list-style: none;
    }
  }
}

// header

#overray-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: calc(var(--vh, 1vh) * 100);
  z-index: 8;
  background-color: rgba(255,255,255,0.8);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s;
  &.active {
    opacity: 1;
    visibility: visible;
  }
  #overray-header-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);

    #global-nav {
      ul {
        li {
          font-size: 1.5rem;
          font-weight: 300;
          line-height: 1;
          margin-bottom: 30px;
          list-style: none;

          &:last-of-type {
            margin-bottom: 0;
          }
        }
      }
    }

    .social {
      display: flex;
      align-items: center;
      justify-content: center;
      @include mq(md) {
        display: none;
      }
      a {
        font-size: 2.6rem;
        color: #9f9f9f;
        line-height: 1;

        &:last-of-type {
          margin-left: 10px;
        }
      }
    }

    #official-link {
      margin-top: 64px;
      a {
        display: flex;
        align-items: center;
        background-color: $red;
        height: 55px;
        padding-left: 15px;
        padding-right: 15px;

        span {
          font-size: 1.3rem;
          line-height: 1;
          color: #fff;
        }

        svg {
          margin-left: 10px;
          width: 30px;
          fill: #fff;

          .st0 {
            fill: #fff;
          }
        }
      }
    }
  }
}

#header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 9;
  background-color: #fff;

  #header-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 70px;
    width: $middle-width;
    @include mq(md) {
      width: auto;
      padding-left: 30px;
      padding-right: 30px;
    }

    a {
      color: $black;
    }

    #logo {
      flex-basis: 30px;

      svg {
        fill: $black;

        .st0-100 {
          fill: #f02800;
        }
      }
    }

    #global-nav {
      flex: 1;
      margin-left: 120px;
      @include mq(md) {
        display: none;
      }

      ul {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        height: 70px;

        li {
          font-size: 1.4rem;
          line-height: 1;
          margin-right: 30px;
          list-style: none;

          &:last-of-type {
            margin-right: 0;
          }
        }
      }
    }

    .social {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      @include mq(md) {
        display: none;
      }
      a {
        font-size: 2.6rem;
        color: #9f9f9f;
        line-height: 1;

        &:last-of-type {
          margin-left: 10px;
        }
      }
    }
  }

  #official-link {
    margin-left: 15px;
    @include mq(md) {
      display: none;
    }
    a {
      display: flex;
      align-items: center;
      background-color: $red;
      height: 55px;
      padding-left: 15px;
      padding-right: 15px;

      span {
        font-size: 1.3rem;
        line-height: 1;
        color: #fff;
      }

      svg {
        margin-left: 10px;
        width: 30px;
        fill: #fff;

        .st0 {
          fill: #fff;
        }
      }
    }
  }

  #sp-menu {
    display: none;
    @include mq(md) {
      display: block;
    }
    a {
      ion-icon {
        font-size: 3rem;
      }
    }
  }
}

.home {
  #overray-header {
    background-color: rgba(0,0,0,0.95);
    a {
      color: #fff;
    }
  }
  #header {
    background: transparent;

    #header-container {
      a {
        color: #fff;
      }

      > #logo {
        svg {
          display: none;
          fill: #fff;
        }
      }

      .social {
        a {
          color: #fff;
        }
      }
    }
  }
}

// heroエリア
#hero {
  box-sizing: border-box;
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  background-position: center;
  background-size: cover;
  height: 100vh;
  @include mq(md) {
    height: auto;
    padding-top: 30%;
    padding-bottom: 30%;
    padding-left: 30px;
    padding-right: 30px;
  }

  &:before {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    content: "";
    background-color: rgba(0, 0, 0, 0.4);
  }

  #hero-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    white-space: nowrap;
    @include mq(md) {
      position: relative;
      top: 0;
      left: 0;
      transform: translate(0,0);
    }

    > img {
      display: block;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 26px;
      @include mq(md) {
        width: 30px;
      }
    }

    h1 {
      color: #fff;
      font-size: 2rem;
      letter-spacing: 2px;
      line-height: 1;
      @include mq(md) {
        font-size: 1rem;
      }
    }

    h2 {
      color: #fff;
      font-size: 4rem;
      font-weight: 700;
      letter-spacing: 2px;
      line-height: 1;
      margin-top: 15px;
      @include mq(md) {
        font-size: 2rem;
      }
    }

    #news {
      margin-top: 32px;
      ul {
        li {
          font-weight: 700;
          list-style: none;
          font-size: 1.4rem;
          color: #FFF;
          white-space: normal;
          @include mq(md) {
            text-align: left;
          }
        }
      }
    }

    #hero-description {
      margin-top: 15px;
      color: #fff;
      font-size: 1.4rem;
      letter-spacing: 1px;
      @include mq(md) {
        font-size: 1.1rem;
        white-space: normal;
        br {
          display: none;
        }
      }
    }

    #table-of-contents {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 32px;
      @include mq(md) {
        display: block;
      }

      li {
        font-size: 1.6rem;
        margin-left: 1em;
        margin-right: 1em;
        list-style: none;
        @include mq(md) {
          font-size: 1.3rem;
          margin-left: .4em;
          margin-right: .4em;
          margin-bottom: 2em;
          &:last-child {
            margin-bottom: 0;
          }
        }

        a {
          color: #fff;
          display: flex;
          align-items: center;
          justify-content: center;
          padding: 10px 20px;
          border: 1px solid #fff;
          border-radius: 20px;
          line-height: 1;
          @include mq(md) {
            justify-content: flex-start;
            padding: 0;
            border: none;
            border-radius: 0;
          }

          &:before {
            font-family: "Material Icons";
            content: "\e5cf";
            margin-right: 4px;
          }
        }
      }
    }

    #support-link {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 32px;
      background-color: rgba(239, 4, 31, 0.7);
      padding: 16px;
      text-align: left;
      color: #fff;
      font-size: 1.3rem;
      box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
      letter-spacing: 1px;
      @include mq(md) {
        display: block;
        font-size: 1.2rem;
      }

      .description {
        flex: 1;
        margin: 0;
        @include mq(md) {
          flex: auto;
          white-space: normal;
          br {
            display: none;
          }
        }
      }

      .more {
        margin-left: 100px;
        flex-basis: 200px;
        @include mq(md) {
          margin-top: 16px;
          margin-left: 0;
        }
        a {
          display: flex;
          flex-wrap: nowrap;
          align-items: center;
          justify-content: center;
          padding: 12px 18px;
          border-radius: 30px;
          background-color: #fff;
          line-height: 1;
          font-size: 1.4rem;
          color: $red;
          font-weight: 700;
          @include mq(md) {
            font-size: 1.2rem;
          }
        }
      }
    }
  }
}

//アワード実施の背景
#past-stories-banner {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  @include mq(md) {
    display: block;
  }
  > a {
    display: flex;
    justify-content: center;
    flex-direction: column;
    box-sizing: border-box;
    flex-basis: 360px;
    height: 166px;
    background-image: url(../../uploads/bg_paststory.jpg);
    color: #FFF;
    padding: 20px 30px;
    margin-bottom: 1px;
    position: relative;
    &:after {
      font-family: 'Material Icons';
      content: "\e5cc";
      position: absolute;
      right: 30px;
      font-size: 5rem;
      @include mq(md) {
        right: 15px;
      }
    }
    @include mq(md) {
      flex-basis: auto;
      padding: 16px 15px;
      height: auto;
    }
    > mark {
      color: #FFF;
      display: block;
      line-height: 1;
      background: none;
      font-size: 4.8rem;
      @include mq(md) {
        font-size: 3.2rem;
      }
    }
  }
}

// アワードの概要
#overview {
  .section-body {
    @include mq(md) {
      padding-left: 30px;
      padding-right: 30px;
    }
  }
}


// キャンペーン
#campaign {
  position: relative;
  z-index: 2;
  margin-top: -140px;
  .section-container {
    text-align: center;
    padding-top: 0;
    padding-bottom: 0;
  }

  #campaign-container {
    display: flex;
    align-items: center;
    justify-content: center;
    .campaign {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      flex-basis: 240px;
      height: 240px;
      background-color: $red;
      color: #FFF;
      border-radius: 100%;
      h2,
      h3 {
        color: #fff;
        line-height: 1;
        margin-bottom: 15px;
        font-weight: 600;
      }
      h2 {
        font-size: 1.8rem;
        line-height: 1;
      }
      h3 {
        font-size: 3.6rem;

      }
      p {
        font-size: 1.2rem;
      }
    }
  }

  h2 {
    font-size: 3.6rem;
    line-height: 1;
    font-weight: 700;
    margin-bottom: 32px;
    color: $black;
  }

  h3 {
    font-size: 2.8rem;
    line-height: 1;
  }
}

// 受賞のメリット
#merit {
  @include mq(md) {
    padding-left: 30px;
    padding-right: 30px;
  }
  #merit-image-container {
    margin-bottom: 64px;
  }

  #merit-image-container,
  #media-sample,
  #event-sample {
    h3 {
      line-height: 1;
      font-size: 2rem;
      font-weight: 700;
      margin-bottom: 32px;
      @include mq(md) {
        text-align: center;
        font-size: 1.3rem;
        margin-bottom: 16px;
      }
    }
  }


  #gallery {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    @include mq(md) {
      display: block;
    }
    figure {
      flex-basis: 32%;
      text-align: center;
      margin-bottom: 24px;
    }

    img {
      display: block;
      margin-left: auto;
      margin-right: auto;
      width: 100%;
    }
    figcaption {
      font-size: 1.4rem;
      padding-top: 12px;
      @include mq(md) {
        font-size: 1.2rem;
        padding-top: 6px;
      }
    }
  }

  #merit-image {
    border-radius: 10px;
    padding: 32px;
    background-color: #fff;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    @include mq(md) {
      display: block;
    }
    .img {
      @include mq(md) {
        text-align: center;
      }
      img {
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
        @include mq(md) {
          max-width: 100px;
        }
      }
    }


    figcaption {
      margin-left: 32px;
      flex: 1;
      font-size: 1.6rem;
      @include mq(md) {
        margin-top: 16px;
        margin-left: 0;
        font-size: 1.2rem;
      }
      a {
        color: $red;
        text-decoration: underline;
      }
    }
  }

  #media-sample {
    margin-bottom: 64px;
  }
}

#past-stories-banner-container {
  padding-bottom: 64px;
}

// 応募要項
#requirements {
  #deadline-container {
    text-align: center;
  }

  #application-deadline {
    font-size: 2rem;
    @include mq(md) {
      font-size: 1.4rem;
    }

    mark {
      display: inline-block;
      background-color: $red;
      color: #fff;
      line-height: 1;
      padding: 8px 10px;
      border-radius: 40px;
      font-size: 1.4rem;
      font-weight: 700;
      margin-bottom: 16px;
      @include mq(md) {
        font-size: 1rem;
      }
    }

    .date {
      font-size: 4.8rem;
      line-height: 1;
      font-weight: 700;
      letter-spacing: 3px;
      @include mq(md) {
        font-size: 2.5rem;
      }
      small {
        font-size: 2.4rem;
        font-weight: 300;
        @include mq(md) {
          font-size: 1rem;
        }
      }
    }
  }

  .section-body {
    margin-top: 64px;
    @include mq(md) {
      padding-left: 30px;
      padding-right: 30px;
    }
  }
}

// アワード受賞者の決定方法
#judgement {

  .judgement-container {
    margin-bottom: 64px;
    counter-increment: judgement;
  }

  #judgement-special-container {
    > h2 {
      line-height: 1;
      font-size: 3rem;
      font-weight: 700;
      text-align: center;
      @include mq(md) {
        font-size: 1.4rem;
      }
    }
    > .lead {
      margin-top: 30px;
      text-align: center;
      font-size: 1.6rem;
      @include mq(md) {
        margin-top: 15px;
        font-size: 1rem;
      }
    }
    .judgement-special-container {
      margin-top: 60px;
      .judgement-special {
        margin-bottom: 30px;
        background-color: #fff;
        border-radius: 10px;
        padding: 55px;
        @include mq(md) {
         padding: 30px;
        }
        &:last-of-type {
          margin-bottom: 0;
        }
        h3 {
          font-size: 2rem;
          font-weight: 700;
          @include mq(md) {
            font-size: 1.6rem;
          }
        }
        .judgement-special-conent {
          margin-top: 16px;
          font-size: 1.6rem;
          font-weight: 300;
          @include mq(md) {
            font-size: 1rem;
          }
        }
      }
    }
  }

  .judgement-title {
    margin-bottom: 64px;
    display: flex;
    align-items: center;
    padding-top: 32px;
    border-top: 1px solid #CCC;
    @include mq(md) {
      //padding-top: 24px;
      margin-bottom: 32px;
      margin-left: 30px;
      margin-right: 30px;
      align-items: flex-start;
    }
    h3 {
      line-height: 1;
      font-size: 3rem;
      font-weight: 700;
      display: flex;
      align-items: center;
      justify-content: center;
      align-items: center;

      @include mq(md) {
        font-size: 1.4rem;
      }

      &:before {
        content: counter(judgement'. ');
        display: block;
        margin-right: 12px;
      }
    }

    p {
      font-size: 1.6rem;
      flex: 1;
      margin-left: 64px;
      &:before {
        display: none;
      }
      @include mq(md) {
        margin-left: 32px;
        font-size: 1rem;
        @include lhCrop(1.5);
      }
    }
  }

  .theme-container {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    justify-content: flex-start;
    margin-left: -30px;
    margin-right: -30px;
    @include mq(md) {
      margin-left: 0;
      margin-right: 0;
      padding-left: 30px;
      padding-right: 30px;
    }
    .theme-card {
      width: 540px;
      background-color: #fff;
      border-radius: 10px;
      padding: 55px;
      box-sizing: border-box;
      margin-left: 30px;
      margin-right: 30px;
      margin-bottom: 60px;
      position: relative;
      counter-increment: theme;
      &:last-of-type {
        margin-bottom: 0;
      }
      @include mq(md) {
        width: 100%;
        padding: 30px;
        margin-left: 0;
        margin-right: 0;
        margin-bottom: 30px;
      }

      > * {
        margin-bottom: 30px;
        @include mq(md) {
          margin-bottom: 15px;
        }
        ul {
          margin-left: 0;
          li {
            list-style: none;
          }
        }
      }

      h4 {
        font-size: 2rem;
        font-weight: 700;
        line-height: 1.5;
        display: flex;
        align-items: center;
        @include mq(md) {
          font-size: 1.6rem;
        }
        span {
          flex: 1;
          @include lhCrop(1.5);
        }

        &:before {
          content: counter(theme);
          font-size: 8rem;
          margin-right: 36px;
          line-height: 1;
          @include mq(md) {
            font-size: 4rem;
            margin-right: 24px;
          }
        }
      }

      .theme-example {
        font-size: 1.2rem;
        @include mq(md) {
          font-size: 1.1rem;
        }
      }

      .theme-description {
        font-size: 1.2rem;
        text-align: justify;
        @include mq(md) {
          font-size: 1.1rem;
        }
      }

      .theme-keywords {
        font-size: 1.2rem;
        position: relative;
        @include mq(md) {
          padding-top: 15px;
        }
        @include mq(md) {
          font-size: 1.1rem;
        }

        &:before {
          display: block;
          content: "キーワード";
          font-weight: 700;
          line-height: 1;
          font-size: 1.2rem;
          margin-bottom: 10px;
          @include mq(md) {
            font-size: 1.1rem;
          }
        }
      }
    }

    .past-stories {
      font-size: 1.2rem;
      @include mq(md) {
        font-size: 1.1rem;
      }

      &:before {
        display: block;
        content: "過去の一次審査通過ストーリー";
        font-weight: 700;
        line-height: 1;
        font-size: 1.2rem;
        margin-bottom: 10px;
        @include mq(md) {
          font-size: 1.1rem;
        }
      }

      a {
        color: #4B5157;
        text-decoration: underline;
      }
    }
  }
}

